.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 15vmin;
}

.App {
  background-color: #eeeeee;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-ui {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 75vw;
  background: black;
  color: white;
  overflow: hidden;

  font-size: calc(10px + 2vmin);
}

.App-prompt {
  display: flex;
  flex-direction: row;
}

.App-cmdline {
  opacity: 0.5;
}

.App-input {
  font-size: calc(10px + 2vmin);
  border: none;
  background: none;
  color: white;
  flex-grow: 1;
  outline: none;
}

.App-results {
  list-style-type: none;
  margin: 0;
  padding: 0;
  height: 50vmin;
  color: #ccc;
}

.App-results b {
  color: #fff;
}

