
.Terminal {
  background: #111;
  border: 1px solid #252525;
  border-radius: 0.5rem;
  box-shadow: 0 0 4rem rgba(0,0,0,0.4);
}

.Terminal-bar {
  height: 2rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #252525;
  justify-content: space-between;
}

.Terminal-content {
  font-family: 'Inconsolata', monospace;
  color: #fff;
  padding: 0.5rem;
}

.Terminal-content input {
  font-family: 'Inconsolata', monospace;
  color: #fff;
}
